import "./styles.css";

function createRosterItem(studentName, studentPronouns, studentHS) {
  let rosterItem = (
    <div className="studentRow">
      <div className="studentData name">{studentName}</div>
      <div className="studentData pronouns">{studentPronouns}</div>
      <div className="studentData highschool">{studentHS}</div>
    </div>
  );
  return rosterItem;
}

export default function App() {
  let people = [
    {
      name: "Mikkail Allan",
      pronouns: "He/His",
      school: "Williamsburg Charter Highschool"
    },
    { name: "Justin Apupalo", pronouns: "He/His", school: "NYIT" }
  ];
  let RosterItem = [];

  var arrayLength = people.length;
  for (var i = 0; i < arrayLength; i++) {
    console.log(people[i].name);
    RosterItem.push(
      createRosterItem(people[i].name, people[i].pronouns, people[i].school)
    );
    console.log(i);
  }

  return (
    <div className="App">
      <h1>Code Nation Roster</h1>
      {RosterItem}
    </div>
  );
}
